.lds-grid {
  display: grid;
  grid-template-columns: 16px 16px 16px;
  grid-template-rows: auto auto;
  grid-gap: 12px;
  position: relative;
  width: 64px;
  height: 64px;
  margin: 0 auto;
}

.lds-grid .dot {
  width: 16px;
  height: 16px;
  animation: lds-grid 1.5s linear infinite;
}

.lds-grid .dot:nth-child(1) {
  animation-delay: 0s;
}

.lds-grid .dot:nth-child(2) {
  animation-delay: -0.3s;
}

.lds-grid .dot:nth-child(4) {
  animation-delay: -0.3s;
