/*
Jumbotron

Jumbotron are hero content areas. Larger marketing text with a colored backgrounds to grab the user or mark the page as important. By default Jumbotron is dark text on a light grey background the UC's add some predefined colored backgrounds allow for modificaiton of the area. The backgrounds were generated by this online generator https://qrohlf.com/trianglify-generator/.
| Class        | Effect                                               |
| ------------ | ---------------------------------------------------- |
| .orange-blue | orange to blue background                            |
| .orange-grey | orange to grey background                            |
| .blue-grey   | blue to grey background                              |
| .soft        | softens the edges of the triangles in the background |
| .blur        | blurs the back to a smooth gradient                  |

.orange-blue 		      - 	orange to blue background
.orange-grey 		      - 	orange to grey background
.blue-grey 			      - 	blue to grey background
.soft				      - 	softens the edges of the triangles in the background 
.blur.blue-grey 	      - 	blurs the back to a smooth gradient

 Markup:
<div class="jumbotron relative overflow_hidden br_round {{modifier_class}}">
   <div class="container p-x_4 max-w_4 w_auto m_auto">
         <h1 class="c_black font_10:lg font_6 font_8:md">I am a Jumbotron!</h1>
         <p class="font_1 font_1:md font_3:lg">Doloribus omnis facere voluptatibus beatae.</p>
         <div><a class="br_radius btn btn-primary gradient_teal h:gradient_teal-reverse" href="#" role="button">Learn more</a></div>
   </div>
</div>

Weight: 50

Styleguide Recipes CVQualtiy.Containers.Jumbotron

*/
.jumbotron:before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 1;
  display: block;
  background: url("../img/cvquality/pattern-grey.png") no-repeat center center;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}
.jumbotron.orange-blue:before {
  background-image: url("../img/cvquality/pattern-orange-blue.png");
}
.jumbotron.orange-grey:before {
  background-image: url("../img/cvquality/pattern-orange-grey.png");
}
.jumbotron.blue-grey:before {
  background-image: url("../img/cvquality/pattern-blue-grey.png");
}
.jumbotron.soft:before {
  -webkit-filter: blur(3px);
  -moz-filter: blur(3px);
  -o-filter: blur(3px);
  -ms-filter: blur(3px);
  filter: blur(3px);
  top: -3px;
  bottom: -3px;
  left: -3px;
  right: -3px;
}
.jumbotron.blur:before {
  -webkit-filter: blur(25px);
  -moz-filter: blur(25px);
  -o-filter: blur(25px);
  -ms-filter: blur(25px);
  filter: blur(25px);
  top: -25px;
  bottom: -25px;
  left: -25px;
  right: -25px;
}
.jumbotron .container {
  z-index: 3;
}

/*
Triangles

Colored Triangles are used through out the cvquality design.  To add triangle that adhears to the side of a container and takes on the color of the border color of the container. 

.triangle_left   -  left placed triangle.
.triangle_right   -  right placed triangle.
.triangle_bottom   -  bottom placed triangle.
.triangle_top   -  top placed triangle.

Markup:
<div class="bg_secondary-5 br_1 br_accent br_radius card highlighted shadow_1 ">
 <div class="{{modifier_class}} br_accent"><h2 class="bg_tansparent br-b_0 c_accent c_secondary-n4 card-header font_1 font_2:md font_bold font_display p-b_3 p-x_3 p-x_3:md p-x_4:lg uppercase">Highlighted Card</h2>
  <div class="card-body font_0 font_1:md font_ui lh_2 p-b_0 p-x_3 p-x_3:md p-x_4:lg p-y_0">
    <p>Repudiandae adipisci illum omnis et suscipit at amet ex. Error magni laborum autem et rerum porro asperiores sunt. Et veritatis rerum voluptas hic eum sint. Ipsa labore eos autem cupiditate ut molestiae sit fugiat. Rerum sit dolorem veritatis est quia nesciunt tenetur ut. Animi earum illo sed incidunt fuga.</p>
  </div>
   <div class="bg_tansparent br-t_0 card-footer p-t_0 p-x_3 p-x_3:md p-x_4:lg"> <a href="#" class="btn btn-block btn-primary font_0:md font_n1">Action</a></div></div>
</div>

Styleguide Recipes CVQualtiy.Decorator.Triangles
*/
*[class*=triangle] {
  position: relative;
}

*[class*=triangle]::before {
  content: "";
  width: 0;
  height: 0;
  border-style: solid;
  position: absolute;
}

.triangle_left::before,
.triangle_right::before {
  top: 50%;
  margin-top: -0.5em;
}

.triangle_top::before,
.triangle_bottom::before {
  left: 50%;
  margin-left: -0.5em;
}

.triangle_left {
  margin-left: 0.75em;
}

.triangle_right {
  margin-right: 0.75em;
}

.triangle_top {
  margin-top: 0.75em;
}

.triangle_bottom {
  margin-bottom: 0.75em;
}

.triangle_left::before {
  left: -0.75em;
  border-width: 0.5em 0 0.5em 0.75em;
  border-left-color: inherit;
  border-right-color: #0000;
  border-bottom-color: #0000;
  border-top-color: #0000;
}

.triangle_right::before {
  right: -0.75em;
  border-width: 0.5em 0.75em 0.5em 0;
  border-left-color: #0000;
  border-right-color: inherit;
  border-bottom-color: #0000;
  border-top-color: #0000;
}

.triangle_top::before {
  top: -0.75em;
  border-width: 0.75em 0.5em 0 0.5em;
  border-left-color: #0000;
  border-right-color: #0000;
  border-bottom-color: #0000;
  border-top-color: inherit;
}

.triangle_bottom::before {
  bottom: -0.75em;
  border-width: 0 0.5em 0.75em 0.5em;
  border-left-color: #0000;
  border-right-color: #0000;
  border-bottom-color: inherit;
  border-top-color: #0000;
}

