	Weight: 100

	Styleguide counters.markers

*/
.order-marker {
  display: inline-block;
  height: 1.5em;
  width: 1.5em;
  margin-right: 0;
  margin-bottom: 0;
  padding: 0.75em 0 0 0;
  position: relative;
  margin-right: 0.5em;
  text-align: center;
  box-sizing: border-box;
  line-height: 0;
  vertical-align: top;
}

